@import url(https://fonts.googleapis.com/css2?family=Della+Respira&family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Della+Respira&family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,300;1,400&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
:root {
  --blueColour: #0D8383;
  --purpleColour: #4E1B34;
  --lightPurpleColour: #771D43;
  --darkColour: #0A1014;
  --lightColour: #ebebeb;
  --textLightcolour: #ebebeb;
  --lightGreyColour: #E5E5E5;
  --textMediumColour: #292728dc;
  --textDarkColour: #0A1014;
  --maxWidthSections: 1600px;
  --textUnderlineColour: #B8B8B8;
  --darkBackgroundColour: #151315; 
}
body {
  background-color: #151315;
  background-color: var(--darkBackgroundColour);/* #161616; #E5E5E5;*/
  color: #ebebeb;
  color: var(--textLightcolour);
  font-family: 'Poppins', sans-serif;
}
body, * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;  
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}


/********************************/
/************ Links *************/
/********************************/
a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}
.link-project {
  text-decoration: underline;
  font-weight: 600;
}
.link-project:hover {
  background-color: white;
}


/********************************/
/************ Text **************/
/********************************/
.large-text {
  font-size: 46px;
}
.medium-text {
  font-size: 26px;
}
.body-text-large {
  font-size: 18px;
}
.body-text-normal {
  font-size: 16px;
}


/****************************************/
/******** Fancy Text Underline **********/
/****************************************/
.underline-gradient span {
  background: linear-gradient(0deg, rgba(255,0,255,0) 0%, rgba(255,0,255,0) 16%, #B8B8B8 16%, #B8B8B8 41%, rgba(255,0,255,0) 41%);
  background: linear-gradient(0deg, rgba(255,0,255,0) 0%, rgba(255,0,255,0) 16%, var(--textUnderlineColour) 16%, var(--textUnderlineColour) 41%, rgba(255,0,255,0) 41%);
}
.underline-padding span {
  padding: 0.9em 0.1em 0.7em 0.1em;
  box-decoration-break: clone;
  -webkit-box-decoration-break: clone;
}


/***********************************************************/
/********* Title with vertical line on the left  ***********/
/***********************************************************/
.title-container {
  margin: auto;
  position: relative;
  color: #ebebeb;
  color: var(--lightColour);
  border-left: 10px solid #4E1B34;
  border-left: 10px solid var(--purpleColour);
  opacity: 1;
  z-index: 2;
  -webkit-filter: url(#glow-generic);
  filter: url(#glow-generic);
  margin-bottom: 20px;
}
.title-text {
  font-weight: 300;
  margin: auto;
  text-align: left;
  padding: 20px 30px;
}


/*********************************/
/************ Media *************/
/*********************************/
@media(max-width: 600px) {
  .large-text {
    font-size: 34px;
  }
  .medium-text {
    font-size: 26px;
  }
  .body-text-large {
    font-size: 16px;
  }
}
#hero-section {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  height: 95vh;
}

/***************** Text in the about section *************************/
#hero-text-container {
  color: var(--textLightcolour);
  position: absolute;
  top: 28vh;
  left: 0;
  z-index: 2;
  width: 500px;
  margin-left: 100px;
}
.name {
  font-size: 85px;
  font-family: 'Della Respira', serif;
  font-weight: 400;
  text-shadow: 4px 4px 0px rgba(78, 27, 52, 0.3);
  mix-blend-mode: screen;
  cursor: default;
}
.personal-description {
  font-size: 1.2em;
  font-weight: 200;
}

/******************** Viz in the header *****************************/
#header-viz-container {
  position: absolute;
  top: 50px;
  left: -100px;
  width: 100%;
  text-align: right;
}


/***************************** Menu  *********************************/
#hero-menu-container {
  margin-top: 40px;
  width: 400px;
  margin-left: 100px;
  color: var(--lightColour);
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 300;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  z-index: 2;
}
.menu-item {
  padding: 10px;
  text-decoration: none;
  margin-right: 20px;
  cursor: default; /* For Safari 3.0 to 6.0 */
  transition: background-color 1s; /* For modern browsers */
}
.menu-item:hover {
  background-color: rgba(13, 131, 131, 0.5);
}
.menu-underline {
  background-color: var(--blueColour);
  height: 10px;
  margin-left: 100px;
  width: 408px;
  -webkit-filter: url(#static-noise-hero);
  filter: url(#static-noise-hero);
  opacity: 1;
}

/****************************************************************/
/******************** Media Queries *****************************/
/****************************************************************/

/* Tablet */

/* iPad Pro */
@media(min-width: 1000px) and (min-height: 1200px) {
  #hero-section {
    height: 55vh;
  }
  #hero-text-container {
    top: 17vh;
  }  
  .personal-description {
    width: 400px;
  }
  .name {
    font-size: 70px;
  }
}
/* Regular iPad */
@media(min-width: 700px) and (max-width: 1000px) and (min-height: 900px) and (max-height: 1200px) {
  #hero-section {
    height: 95vh;
  }
  #hero-text-container {
    bottom: 10vh;
    width: 400px;
  }  
  .name {
    font-size: 80px;
    line-height: 1.2;
    margin-bottom: 15px;
  }
}


@media(max-width: 1000px) {
  #hero-text-container {
    top: 38vh;
  }  
  #header-viz-container {
    top: 6vh;
  }
}

@media(max-width: 650px) {
  #hero-text-container, #hero-menu-container, .menu-underline {
    margin-left: 70px;
  } 
  #hero-text-container {
    width: 450px;
    top: 35vh;
  }
  .name {
    font-size: 65px;
  }
  .personal-description {
    font-size: 1.0em;
  }
  #header-viz-container {
    top: 10vh;
  }
}

/* Very large phones or small tablets */
@media(max-width: 500px) {
  #hero-text-container {
    margin-left: 50px;
    width: 350px;
    top: 36vh;
  } 
  #hero-menu-container, .menu-underline {
    width: 80%;
    margin-left: 10%;
    font-size: 14px;
  }
  .name {
    font-size: 65px;
    line-height: 1.2;
    margin-bottom: 15px;
  }
  #header-viz-container {
    top: 10vh;
    left: -30px;
  } 
}

/* Medium-large phones like the Pixel 2 XL */
@media(max-width: 420px) {
  #hero-text-container {
    width: 300px;
    top: 36vh;
  }
  #hero-menu-container, .menu-underline {
    width: 100%;
    margin-left: 0;
  }
  #hero-menu-container {
    margin-top: 15px;
  }
  .menu-item {
    margin-right: 0px;
  }
  .name {
    font-size: 55px;
  }
}

/* Small phone like older iPhones*/
@media(max-width: 360px) {
  #hero-text-container {
    margin-left: 30px;
  } 
}

/* very small phone like the Galaxy Fold */
@media(max-width: 320px) {
  #hero-text-container {
    margin-left: 20px;
    width: 260px;
  } 
  #hero-menu-container {
    font-size: 12px;
  }
}


